body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'San Francisco', Helvetica, Arial;
  /*'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;*/
  font-size: medium;
}

#page-body {
  max-width: 960px;
  margin: auto;
  padding: 64px 10px 30px 10px;
  top: 40px;
}

h3,
h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 3px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  z-index: 1;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: 300;
  padding: 16px 28px;
  margin: 2px;
}

nav a:hover {
  font-weight: bold;
  color: black;
}

#page-body {
  max-width: 700px;
  margin: auto;
  padding-top: 64px;
}

:-webkit-any-link {
  text-decoration: none;
}

:any-link {
  text-decoration: none;
}

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid black;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input,
textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button,
p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

footer {
  border-top: 3px solid black;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  background: rgb(255, 255, 255);
  opacity: 1;
  padding: 3px;
  font-size: x-small;
}

footer a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 3px 3px;
}

footer a:hover {
  background: black;
  color: white;
}

img {
  border-radius: 5px;
  margin: 0.6rem;
}
